@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,500;1,400&display=swap);
.hero #col-1{height:75vh;background-color:#fff;background-image:url("https://www.transparenttextures.com/patterns/crissxcross.png")}.hero #col-2{height:75vh;background-color:#77b7ff;background-image:url("https://www.transparenttextures.com/patterns/crissxcross.png");background-position:106px 23px}#home-section{min-height:100vh}#home-section .imgSection{z-index:1;position:absolute;height:95vh;width:54vw;transform:translate(50%, -68%);box-shadow:39px 30px 57px -55px rgba(0,0,0,.75)}#home-section .imgSection .imgContent{position:absolute;top:20vh;float:left;font-size:2.5em;z-index:1}#home-section .imgSection .imgContent p{font-size:.6em;text-align:left}#home-section .imgSection .imgContent span{color:#77b7ff;font-weight:1000;font-family:"Gill Sans","Gill Sans MT",Calibri,"Trebuchet MS",sans-serif}#home-section .imgSection img{float:right;width:auto;height:100%}@media(max-width: 1168px){#home-section .imgSection img{z-index:0;-webkit-filter:brightness(50%);filter:brightness(50%)}#home-section .imgSection .imgContent{color:#fff}#home-section .imgSection p{color:#fff}}
html,body{margin:0;padding:0;overflow-x:hidden;font-family:"Montserrat",sans-serif;transition:1s}
.aboutMe{min-height:100vh}.aboutMe h1{font-weight:800}.aboutMe h3{font-size:3ex;font-weight:200}.aboutMe p{font-size:1.8ex;color:gray}.aboutMe .aboutMeCard{min-width:8vw;max-width:19rem;float:none;margin:0 auto}.aboutMe .aboutMeCard h5{font-size:2ex}.aboutMe .aboutMeCard p{font-size:1.8ex;color:gray;justify-content:baseline}.aboutMe .aboutMeCard img{width:100%}.ProjectsDone{width:100%;min-height:10vh;background-color:#0075f6;color:#fff}.ProjectsDone h1{font-size:3.5ex;font-weight:300}#projects-done:hover svg{-webkit-animation:repeat-leftRight 1s ease-in-out infinite;animation:repeat-leftRight 1s ease-in-out infinite}@-webkit-keyframes repeat-leftRight{0%{margin-left:0rem}50%{margin-left:1rem}100%{margin-left:0rem}}@keyframes repeat-leftRight{0%{margin-left:0rem}50%{margin-left:1rem}100%{margin-left:0rem}}
.footerBar{min-height:30vh;background-color:#0075f6}.footerBar .footerIcon{border-radius:80%;width:50px;height:50px;background-color:#1e8bff;margin:auto;margin-top:3px}.footerBar .footerIcon a :hover{padding:2px;transition:.1s ease}.footerBar .footerIcon img{width:100%;border-radius:100%;margin-top:3px}.footerBar h5{margin:auto}
.SkillCard{text-align:center;align-content:center;max-width:100vw;min-height:40vh}.SkillCard :hover{transition:500ms ease-in-out}
